a {
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  color: #04A3E9;
  text-decoration: none; }

a:hover, a:focus {
  text-decoration: none; }

img {
  max-width: 100%;
  max-height: 100%;
  width: initial;
  height: initial; }

@media (min-width: 1024px) {
